* {
  box-sizing: border-box;
}
.appleBasket {
  width: 470px;
  margin: 20px auto;
  border-radius: 4px;
  border: 1px solid #ddd;
  position: relative;
}
.appleBasket > .title {
  padding: 15px 0;
  text-align: center;
  color: #069;
  font-size: 22px;
  font-weight: bold;
}

.appleBasket > .stats {
    width: 100%;
    border-top: 1px dashed #ddd;
    border-bottom: 1px dashed #ddd;
    padding: 10px 0;
}

.appleBasket > .section {
    display: inline-block;
    width: 50%;
    padding-left: 8px;
}

.appleBasket > .section  .head{ 
    padding: 6px 0px;
    font-size: 16px;
    color: #069;
}

.appleBasket > .section  .content{ 
 font-size: 20px;
  font-weight: 200;
}

.appleBasket > .section:first-of-type {
  boder-right: 1px solid #f0f0f0;
}

.appleList {
  padding: 10px 0;
}
.empty-tip {
  text-align: center;
  font-size: 16px;
  color: #ccc;
  padding: 20px 0;
}

.btn-div {
  text-align: center;
}

.appleItem {
    width: 420px;
    margin: 10px auto;
    border-radius: 4px;
    border: 1px solid #ddd;
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    align-items: center;
 }

 .appleItem > .apple {
    flex-grow: 2;
 }
 
 .info {
   flex-grow: 15;
 }

 .info > .name {
    padding: 6px 0;
    font-size: 20px;
    color: #069;
    font-weight: 500;
 }

 .info > .weight {
  font-size: 16px;
  font-weight: 200;
 }

 .btn-div {
   flex-grow: 2;
 }
 .btn-div button {
   border: none;
   background-color: #3498db;
   color: #fff;
   font-size: 14px;
   cursor: pointer;

   padding: 10px 24px;
   border-radius: 6px;
   margin: 18px auto;
   outline: none;
 }

 .btn-div button :hover {
  background-color: #5dade2
 }
